'use client';

import { useState, useMemo } from 'react';
import { reviews, getReviewsForProduct, getReviewSummary, sortReviews, filterReviewsByRating, Review } from '@/data/reviews';

interface ReviewsDisplayProps {
  productId: string;
  className?: string;
}

export default function ReviewsDisplay({ productId, className = '' }: ReviewsDisplayProps) {
  const [sortBy, setSortBy] = useState<'newest' | 'oldest' | 'highest' | 'lowest' | 'helpful'>('helpful');
  const [filterRating, setFilterRating] = useState<number | null>(null);
  const [showAllReviews, setShowAllReviews] = useState(false);
  const [helpfulVotes, setHelpfulVotes] = useState<{ [key: string]: { helpful: number; total: number } }>({});

  // Get reviews for this specific product
  const productReviews = useMemo(() => {
    const baseReviews = getReviewsForProduct(productId);
    const filteredReviews = filterReviewsByRating(baseReviews, filterRating);
    const sortedReviews = sortReviews(filteredReviews, sortBy);
    return showAllReviews ? sortedReviews : sortedReviews.slice(0, 3);
  }, [productId, sortBy, filterRating, showAllReviews]);

  const summary = useMemo(() => getReviewSummary(productId), [productId]);

  const handleHelpfulVote = (reviewId: string, isHelpful: boolean) => {
    setHelpfulVotes(prev => {
      const current = prev[reviewId] || { helpful: 0, total: 0 };
      return {
        ...prev,
        [reviewId]: {
          helpful: current.helpful + (isHelpful ? 1 : 0),
          total: current.total + 1
        }
      };
    });
  };

  // Star Rating Component
  const StarRating = ({ rating, size = 'sm', showNumber = false }: { rating: number; size?: 'sm' | 'md' | 'lg'; showNumber?: boolean }) => {
    const sizeClasses = {
      sm: 'w-4 h-4',
      md: 'w-5 h-5',
      lg: 'w-6 h-6'
    };

    return (
      <div className="flex items-center gap-2">
        <div className="flex items-center">
          {[1, 2, 3, 4, 5].map((star) => (
            <svg
              key={star}
              className={`${sizeClasses[size]} ${star <= Math.floor(rating) ? 'text-yellow-400' : 'text-gray-300'}`}
              fill="currentColor"
              viewBox="0 0 20 20"
            >
              <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
            </svg>
          ))}
        </div>
        {showNumber && (
          <span className="text-sm text-gray-600 ml-1">
            {rating.toFixed(1)} out of 5
          </span>
        )}
      </div>
    );
  };

  // Rating Breakdown Component
  const RatingBreakdown = () => (
    <div className="space-y-2">
      {[5, 4, 3, 2, 1].map(rating => {
        const count = summary.ratingBreakdown[rating] || 0;
        const percentage = summary.totalReviews > 0 ? (count / summary.totalReviews) * 100 : 0;
        
        return (
          <button
            key={rating}
            onClick={() => setFilterRating(filterRating === rating ? null : rating)}
            className={`flex items-center w-full text-sm hover:bg-gray-50 p-2 rounded transition-colors ${
              filterRating === rating ? 'bg-orange-50 text-orange-700' : ''
            }`}
          >
            <span className="flex items-center gap-1 w-20">
              <span className="font-medium">{rating}</span>
              <svg className="w-3 h-3 text-yellow-400" fill="currentColor" viewBox="0 0 20 20">
                <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
              </svg>
            </span>
            <div className="flex-1 mx-3">
              <div className="bg-gray-200 rounded-full h-2">
                <div 
                  className="bg-orange-400 h-2 rounded-full transition-all duration-500"
                  style={{ width: `${percentage}%` }}
                />
              </div>
            </div>
            <span className="w-12 text-right text-orange-600 font-medium">{count}</span>
          </button>
        );
      })}
    </div>
  );

  if (summary.totalReviews === 0) {
    return (
      <div className={`bg-white ${className}`}>
        <div className="border-t border-gray-200 pt-8">
          <h2 className="text-2xl font-bold text-gray-900 mb-6">Customer Reviews</h2>
          <div className="text-center py-12">
            <div className="text-gray-400 text-lg mb-4">No reviews yet</div>
            <p className="text-gray-600 mb-6">Be the first to review this product!</p>
            <button className="bg-orange-600 text-white px-6 py-3 rounded-lg hover:bg-orange-700 transition-colors font-semibold">
              Write a Review
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className={`bg-white ${className}`} id="customer-reviews">
      <div className="border-t border-gray-200 pt-8">
        {/* Header */}
        <div className="mb-8">
          <h2 className="text-2xl font-bold text-gray-900 mb-6">Customer Reviews</h2>
          
          {/* Overall Rating Summary */}
          <div className="bg-gray-50 rounded-lg p-6 mb-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {/* Average Rating */}
              <div className="text-center md:text-left">
                <div className="flex flex-col md:flex-row items-center md:items-start gap-4">
                  <div>
                    <div className="text-4xl font-bold text-gray-900 mb-2">
                      {summary.averageRating.toFixed(1)}
                    </div>
                    <StarRating rating={summary.averageRating} size="lg" />
                    <div className="text-sm text-gray-600 mt-2">
                      Based on {summary.totalReviews} review{summary.totalReviews !== 1 ? 's' : ''}
                    </div>
                  </div>
                </div>
              </div>
              
              {/* Rating Breakdown */}
              <div className="md:col-span-2">
                <h3 className="font-semibold text-gray-900 mb-4">Rating Breakdown</h3>
                <RatingBreakdown />
              </div>
            </div>
          </div>
        </div>

        {/* Filters and Sort */}
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-6">
          <div className="flex items-center gap-4">
            <span className="text-sm font-medium text-gray-700">Sort by:</span>
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as any)}
              className="border border-gray-300 rounded-md px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
            >
              <option value="helpful">Most Helpful</option>
              <option value="newest">Newest First</option>
              <option value="oldest">Oldest First</option>
              <option value="highest">Highest Rating</option>
              <option value="lowest">Lowest Rating</option>
            </select>
          </div>
          
          {filterRating && (
            <button
              onClick={() => setFilterRating(null)}
              className="text-sm text-orange-600 hover:text-orange-800 flex items-center gap-1"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
              Clear {filterRating} star filter
            </button>
          )}
        </div>

        {/* Reviews List */}
        <div className="space-y-6">
          {productReviews.map((review) => {
            const currentVotes = helpfulVotes[review.id] || { helpful: 0, total: 0 };
            const totalHelpful = review.helpfulVotes + currentVotes.helpful;
            const totalVotes = review.totalVotes + currentVotes.total;
            
            return (
              <div key={review.id} className="border border-gray-200 rounded-lg p-6 hover:shadow-sm transition-shadow">
                {/* Review Header */}
                <div className="flex items-start justify-between mb-4">
                  <div className="flex items-start space-x-3">
                    {/* Avatar */}
                    <div className="w-10 h-10 bg-orange-100 rounded-full flex items-center justify-center">
                      {review.userAvatar ? (
                        <img src={review.userAvatar} alt={review.userName} className="w-10 h-10 rounded-full object-cover" />
                      ) : (
                        <span className="text-sm font-medium text-orange-600">
                          {review.userName.charAt(0)}
                        </span>
                      )}
                    </div>
                    
                    {/* User Info */}
                    <div>
                      <div className="font-medium text-gray-900">{review.userName}</div>
                      <div className="text-sm text-gray-500">
                        {new Date(review.createdAt).toLocaleDateString('en-IN', {
                          year: 'numeric',
                          month: 'long',
                          day: 'numeric'
                        })}
                      </div>
                    </div>
                  </div>
                  
                  {/* Share Button */}
                  <button className="p-2 text-gray-400 hover:text-gray-600 rounded-full hover:bg-gray-100 transition-colors">
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
                    </svg>
                  </button>
                </div>

                {/* Rating and Verified Purchase */}
                <div className="flex items-center gap-4 mb-3">
                  <StarRating rating={review.rating} size="sm" />
                  {review.verifiedPurchase && (
                    <span className="bg-orange-100 text-orange-800 text-xs font-medium px-2 py-1 rounded">
                      ‚úì Verified Purchase
                    </span>
                  )}
                </div>

                {/* Review Title */}
                <h3 className="font-semibold text-gray-900 mb-2 text-lg">{review.title}</h3>

                {/* Review Content */}
                <p className="text-gray-700 mb-4 leading-relaxed">{review.content}</p>

                {/* Review Images */}
                {review.images && review.images.length > 0 && (
                  <div className="flex gap-2 mb-4">
                    {review.images.map((image, index) => (
                      <div key={index} className="w-20 h-20 bg-gray-200 rounded border cursor-pointer hover:opacity-80 transition-opacity">
                        <img
                          src={image}
                          alt={`Review image ${index + 1}`}
                          className="w-full h-full object-cover rounded"
                        />
                      </div>
                    ))}
                  </div>
                )}

                {/* Helpful Votes */}
                <div className="flex items-center justify-between pt-4 border-t border-gray-100">
                  <div className="flex items-center gap-4">
                    <span className="text-sm text-gray-600">Was this helpful?</span>
                    <div className="flex gap-2">
                      <button
                        onClick={() => handleHelpfulVote(review.id, true)}
                        className="text-sm text-gray-600 hover:text-gray-800 px-4 py-2 border border-gray-300 rounded-md hover:bg-gray-50 transition-colors"
                      >
                        üëç Yes
                      </button>
                      <button
                        onClick={() => handleHelpfulVote(review.id, false)}
                        className="text-sm text-gray-600 hover:text-gray-800 px-4 py-2 border border-gray-300 rounded-md hover:bg-gray-50 transition-colors"
                      >
                        üëé No
                      </button>
                    </div>
                  </div>
                  
                  {totalVotes > 0 && (
                    <div className="text-sm text-gray-500">
                      {totalHelpful} of {totalVotes} people found this helpful
                    </div>
                  )}
                </div>
              </div>
            );
          })}
        </div>

        {/* Load More / Write Review */}
        <div className="mt-8 flex flex-col sm:flex-row gap-4 justify-center items-center">
          {!showAllReviews && getReviewsForProduct(productId).length > 3 && (
            <button 
              onClick={() => setShowAllReviews(true)}
              className="border border-gray-300 text-gray-700 px-6 py-3 rounded-lg hover:bg-gray-50 transition-colors font-medium"
            >
              See all {getReviewsForProduct(productId).length} reviews
            </button>
          )}
          
          <button className="bg-orange-600 text-white px-6 py-3 rounded-lg hover:bg-orange-700 transition-colors font-semibold">
            Write a Review
          </button>
        </div>
      </div>
    </div>
  );
}